@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,200;0,300;1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#main_nav .container-fluid {
  padding-right: 0px !important;
  padding-left: 0px !important;
}
#main_nav .bg-dark {
  background-color: #f4783d !important;
}
#main_nav .dropdown-menu.show {
  display: block;
  background-color: #f68241;
  width: 200px;
}
#main_nav .navbar-dark .navbar-nav .nav-link {
  color: #fff;
  font-weight: bold;
}
#main_nav .dropdown-item {
  color: #ffffff;
}
#main_nav .dropdown-item:focus,
.dropdown-item:hover {
  background-color: #e47946;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
}

.images_slides {
  margin-top: -24.5px;
  display: block;
  position: relative;
}
.carousel-inner img {
  width: 100%;
  height: 80vh;
  opacity: 0.7;
  -o-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5 ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.carousel-inner img:hover {
  opacity: 1;
}

body {
  height: 3000px;
}
i {
  color: rgb(0, 0, 0);
}
.about-me {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #f5f5f5;
  overflow: hidden;
  margin-bottom: 20px;
}
.about-me h1 {
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  text-align: center;
  font-weight: bold !important;
  text-transform: uppercase;
  color: #666;
}
.about {
  background-color: #e3e6fb;
  font-family: "Exo 2", sans-serif;
  padding-bottom: 20px;
}

.about .card {
  box-shadow: 5px 6px 12px #cc94a8;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;

  border-radius: 0px;
}

.about .card img {
  width: 150px;

  height: 150px;

  margin: auto;

  border-radius: 50%;
}

.about .card h2,
.about .card h4 {
  font-family: "Exo 2", sans-serif;
}

.about .about-info {
  background-color: #fff;

  height: 100%;

  padding: 20px;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;
}

.about .about-info h2 {
  font-family: Arial, Helvetica, sans-serif;

  font-weight: bold;

  font-family: "Exo 2", sans-serif;

  text-transform: uppercase;
}

.about .about-info h2:before {
  content: "";

  border-bottom: 1px solid #cf4b7c;

  border-width: 4px;

  width: 148px;

  position: absolute;

  top: 58px;
}

.built-with-react {
  width: auto;
  height: 155px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(/mahmoudaosman/static/media/built-with.4bf088b1.png);
  display: block;
  overflow: hidden;
  position: relative;
}

.education {
  width: auto;
  height: auto;
  /* background-color: #e8aa4d; */
  background-color: #c1cee3;
  padding-bottom: 20px;
  position: relative;
  display: block;
}

.education .education-header {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  /* background-color: #ec8876; */
  background-color: #fff;
  overflow: hidden;
  margin-bottom: 20px;
}
.education .education-header h1 {
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  text-align: center;
  color: #666;
  text-transform: uppercase;
}

.education .bacholer,
.education .associate,
.education .certs {
  background-color: #fff;

  height: 100%;

  position: relative;

  display: block;
  -o-box-shadow: 5px 6px 12px #cc94a8;
  -ms-box-shadow: 5px 6px 12px #cc94a8;
  box-shadow: 5px 6px 12px #cc94a8;
}

.education img {
  width: 100%;
  height: auto;
  display: block;
}

.marketing-intro {
  width: 100%;
  min-height: 250px;
  background-color: #f7f7f7;
  position: relative;
  display: block;
  margin-top: 0;
  margin-bottom: -30px;
  padding-bottom: 60px;
  margin-top: -60px;
}

.marketing-intro i,
.marketing-intro i:hover {
  width: 160px;
  height: 160px;

  color: #4e204e;
  text-align: center;
  line-height: 160px;
  margin-left: 10px;
}

.marketing-intro i {
  font-size: 85px;
  cursor: pointer;
  opacity: 0.6;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
  border: 20px solid #f7f7f7;
  border-radius: 50%;
  margin-top: 80px;
}

.marketing-intro i:hover {
  font-size: 45px;
  border: 3px solid #4e204e;
  border-radius: 50%;
  opacity: 1;
}

.marketing-intro h3 {
  font-family: Arial;
  font-size: 14px;
  color: #999;
  margin-bottom: 40px;
  cursor: pointer;
}

.marketing-intro h3:after {
  content: "";
  position: absolute;
  width: 0;
  height: 3px;
  display: block;
  margin-top: 5px;
  right: 0;
  background: #a9e2b4;
  transition: width 0.2s ease;
  -webkit-transition: width 0.2s ease;
}

.marketing-intro h3:hover:after {
  width: 100%;
  left: 0;
  background: #a9e2b4;
}

.main-skills {
  margin-top: 40px;
  background-color: #e3e6fb;
  padding: 22px 0px;
  margin-top: 0px;
}
.main-skills .skill-header {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #fff;
  overflow: hidden;
  margin-bottom: 20px;
  margin-left: -124%;
  width: 138%;
}
.main-skills .skill-header h1 {
  margin-left: 90%;
  width: 138%;
  transform: skew(35deg);
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
}

@media only screen and (max-width: 988px) {
  .main-skills .skill-header {
    margin-left: -87%;
    width: 130%;
  }
  .main-skills .skill-header h1 {
    margin-left: 66%;
    width: 138%;
  }
}

.main-skills h1 {
  color: #666;
}
.main-skills .progress {
  background-color: #ffffff;
  border-radius: 50px;
}
.main-skills .progress-bar {
  border-radius: 50px;
  font-weight: bold;
  color: #0e1d25;
}

